"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { cn } from "@/lib/utils";

type Player = "X" | "O" | null;
type Board = Player[];

const WINNING_COMBINATIONS = [
  [0, 1, 2], [3, 4, 5], [6, 7, 8], // Rows
  [0, 3, 6], [1, 4, 7], [2, 5, 8], // Columns
  [0, 4, 8], [2, 4, 6] // Diagonals
];

export default function TicTacToeGame() {
  const [board, setBoard] = useState<Board>(Array(9).fill(null));
  const [currentPlayer, setCurrentPlayer] = useState<"X" | "O">("X");
  const [winner, setWinner] = useState<Player>(null);
  const [gameOver, setGameOver] = useState(false);
  const [error, setError] = useState<string>("");

  const checkWinner = (newBoard: Board): Player => {
    for (const combination of WINNING_COMBINATIONS) {
      const [a, b, c] = combination;
      if (newBoard[a] && newBoard[a] === newBoard[b] && newBoard[a] === newBoard[c]) {
        return newBoard[a];
      }
    }
    return null;
  };

  const handleCellClick = (index: number) => {
    try {
      setError("");
      
      if (board[index] || winner || gameOver) {
        setError("Invalid move! Cell is already occupied or game is over.");
        return;
      }

      const newBoard = [...board];
      newBoard[index] = currentPlayer;
      setBoard(newBoard);

      const gameWinner = checkWinner(newBoard);
      if (gameWinner) {
        setWinner(gameWinner);
        setGameOver(true);
      } else if (newBoard.every(cell => cell !== null)) {
        setGameOver(true);
      } else {
        setCurrentPlayer(currentPlayer === "X" ? "O" : "X");
      }
    } catch (err) {
      setError("An error occurred while making the move.");
      console.error("Tic Tac Toe error:", err);
    }
  };

  const resetGame = () => {
    try {
      setBoard(Array(9).fill(null));
      setCurrentPlayer("X");
      setWinner(null);
      setGameOver(false);
      setError("");
    } catch (err) {
      setError("An error occurred while resetting the game.");
      console.error("Reset game error:", err);
    }
  };

  const getGameStatus = () => {
    if (winner) return `Player ${winner} wins! üéâ`;
    if (gameOver) return "It's a tie! ü§ù";
    return `Current player: ${currentPlayer}`;
  };

  return (
    <div className="max-w-2xl mx-auto p-4">
      <Card>
        <CardHeader className="text-center">
          <CardTitle className="text-2xl font-bold">Tic Tac Toe</CardTitle>
          <p className="text-muted-foreground">
            Get three in a row to win! Click on any empty cell to make your move.
          </p>
        </CardHeader>
        <CardContent className="space-y-6">
          {/* Game Status */}
          <div className="text-center">
            <p className="text-lg font-semibold text-foreground">
              {getGameStatus()}
            </p>
            {error && (
              <p className="text-destructive text-sm mt-2 bg-destructive/10 p-2 rounded">
                {error}
              </p>
            )}
          </div>

          {/* Game Board */}
          <div className="grid grid-cols-3 gap-2 max-w-xs mx-auto">
            {board.map((cell, index) => (
              <button
                key={index}
                onClick={() => handleCellClick(index)}
                className={cn(
                  "aspect-square w-full h-20 border-2 border-border rounded-lg",
                  "flex items-center justify-center text-2xl font-bold",
                  "transition-all duration-200 hover:bg-accent hover:border-primary",
                  "disabled:cursor-not-allowed disabled:opacity-50",
                  cell === "X" ? "text-blue-600" : "text-red-600",
                  !cell && !gameOver && "hover:shadow-md"
                )}
                disabled={!!cell || gameOver}
              >
                {cell}
              </button>
            ))}
          </div>

          {/* Game Controls */}
          <div className="flex justify-center space-x-4">
            <Button onClick={resetGame} variant="outline">
              New Game
            </Button>
          </div>

          {/* Game Rules */}
          <div className="text-sm text-muted-foreground bg-muted/30 p-4 rounded-lg">
            <h4 className="font-semibold mb-2">How to Play:</h4>
            <ul className="space-y-1">
              <li>‚Ä¢ Players take turns placing X's and O's on the grid</li>
              <li>‚Ä¢ First player to get 3 in a row (horizontal, vertical, or diagonal) wins</li>
              <li>‚Ä¢ If all 9 squares are filled without a winner, it's a tie</li>
            </ul>
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
